import { useParams } from "react-router-dom";
import { decrypt } from "crypto-js/aes";
import { enc } from "crypto-js";
import { config } from "../../config/env";
import { useState } from "react";
import NewTab from "../NewTab/NewTab";

export default function Scan() {
    const { hash } = useParams();
    const [downloadedText, SetDownloadedText] = useState("");
    const [_, locker, token] = decrypt(atob(hash), 'secret').toString(enc.Utf8).split('$$');

    const myHeaders = new Headers();
    myHeaders.append("Content-Type", "application/json");
    fetch(config.api + "supply/loot/text", {
        method: "POST",
        body: JSON.stringify({
            locker: locker?.split('-')[1],
            token: token,
        }), //formData,
        headers: myHeaders,
        // mode: 'no-cors'
    })
        .then((response) => {
            console.log(response);
            return response.text();
        })
        .then((res) => {
            SetDownloadedText(res);
            console.log(res);
        })
        .catch((e) => {
            console.log(e);
        });
    return <div>
        {/* ToDo: This needs to be replaced by a component which shows text on new tab */}
        <NewTab text={downloadedText} />
    </div>
}