import { useEffect, useState } from "react"
import {config} from "../../config/env"
import "./ReadyStatus.scss"


export default function ReadyStatus() {
    const [ready, isReady] = useState(false);
    useEffect(() => {
        fetch(config.endpoint).then(res => {
            if (res.status === 200)
                isReady(true);
        });
    }, [ready]);
    return <span className={'dot ' + (ready? 'success': 'warn')}></span>
}