import React from "react";
import "./TabMenu.scss";
import { assets } from "../../Assets/index";
import LoadSupply from "../LoadSupply/LoadSupply";
import LootDrop from "../LootDrop/LootDrop";

export default function TabMenu() {
  return (
    <div className="container TabMenu" id="box-container">
      <div className="d-flex align-items-start h-100">
        <div
          className="nav flex-column nav-pills me-3"
          id="v-pills-tab"
          role="tablist"
          aria-orientation="vertical"
        >
          <div
            id="v-pills-home-tab"
            className="active mt-5"
            data-bs-toggle="pill"
            data-bs-target="#v-pills-home"
            type="button"
            role="tab"
            aria-controls="v-pills-home"
            aria-selected="true"
          >
            <img className="icons" src={assets.uploadStaticIcon} title="Click to Load Supply"/>
          </div>
          <div
            id="v-pills-profile-tab"
            data-bs-toggle="pill"
            data-bs-target="#v-pills-profile"
            type="button"
            role="tab"
            aria-controls="v-pills-profile"
            aria-selected="false"
            className="mt-5"
          >
            <img className="icons" src={assets.downloadStaticIcon} title="Click to Loot the Drop"/>
          </div>
        </div>
        <div className="tab-content w-100 h-100" id="v-pills-tabContent">
          <div
            className="tab-pane fade show active h-100"
            id="v-pills-home"
            role="tabpanel"
            aria-labelledby="v-pills-home-tab"
          >
            <LoadSupply/>
          </div>
          <div
            className="tab-pane fade h-100"
            id="v-pills-profile"
            role="tabpanel"
            aria-labelledby="v-pills-profile-tab"
          >
            <LootDrop/>
          </div>
        </div>
      </div>
    </div>
  );
}
