import React from "react";
import "./Background.scss";
import {assets} from '../../Assets/index';

export default function Background() {
  return (
    <div id="background">
      <div className="bg">
        <video className="videoTag" autoPlay muted>
            {/* loop */}
          <source src={assets.BGVideo} type="video/mp4" />
        </video>
      </div>
    </div>
  );
}
