import React, { useState } from "react";
import "./LoadSupply.scss";
import Modal from "../Modal/Modal";
import { config } from "../../config/env";
import NewTab from "../NewTab/NewTab";

export default function LoadSupply() {
  const [token, SetToken] = useState("");
  const [text, SetText] = useState("");
  const [file, SetFile] = useState("");
  const [modalText, SetModalText] = useState("");
  const [modalTitle, SetModalTitle] = useState("");
  const [modalToken, SetModalToken] = useState("");
  const UploadSupply = () => {
    console.log(token);
    console.log(text);
    console.log(file);
    if (token == "") {
      alert("Please enter a Token number");
      return;
    }
    if (text != "") {
      const myHeaders = new Headers();
      myHeaders.append("Content-Type", "application/json");
      fetch(config.api + "supply/load/text", {
        method: "POST",
        body: JSON.stringify({
          content: text,
          token: token,
        }), //formData,
        headers: myHeaders,
        // mode: 'no-cors'
      })
        .then((response) => {
          console.log(response);
          return response.text();
        })
        .then((res) => {
          SetModalText(res);
          SetModalToken(token);
          SetModalTitle("Supplies Loaded Successfully");
          var el = document.getElementById("modal");
          el.classList.add("show");
          el.style.display = "block";
          console.log(res);
          SetToken("");
          SetText("");
        })
        .catch((e) => {
          console.log(e);
        });
    }
    if (file != "") {
      const formData = new FormData();
      formData.append("token", token);
      formData.append("doc", document.querySelector("#file").files[0]);
      fetch(config.api + "supply/load/file", {
        method: "POST",
        body: formData,
      })
        .then((response) => {
          console.log(response);
          return response.text();
        })
        .then((res) => {
          SetModalText(res);
          SetModalToken(token);
          SetModalTitle("Supplies Loaded Successfully");
          var el = document.getElementById("modal");
          el.classList.add("show");
          el.style.display = "block";
          console.log(res);
          SetToken("");
          SetFile("");
        })
        .catch((e) => {
          console.log(e);
        });
    }
  };
  return (
    <div className="load-Supply Container">
      <div className="row m-2">
        <div className="form-group token">
          <label>
            Enter a pin or token number which will be used to Loot this supply
          </label>
          <input
            type="text"
            className="form-control"
            placeholder="Enter a Token number"
            required
            value={token}
            onChange={(e) => SetToken(e.target.value)}
          />
        </div>
      </div>
      <div className="row h-md-75">
        <div className="form-group files col-12 col-lg-6">
          <input
            id="file"
            type="file"
            className="form-control"
            multiple=""
            value={file}
            onChange={(e) => SetFile(e.target.value)}
          />
        </div>
        <div className="form-group col-12 col-lg-6 mb-2 pb-2">
          <input
            type="text"
            className="form-control text"
            placeholder="Enter the Text to share"
            value={text}
            onChange={(e) => SetText(e.target.value)}
          />
        </div>
      </div>
      <div className="row m-2">
        <button
          className={"btn btn-success " + (token == "" ? "disabled" : "")}
          onClick={UploadSupply}
        >
          Load Supply
        </button>
      </div>
      <Modal
        title={modalTitle}
        text={modalText}
        token={modalToken}
        openNewTab="false"
      />
    </div>
  );
}
